body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }
  
  section{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center; 
    color: white;
    scroll-snap-align: start;
  }
  
  container{
    scroll-snap-type: y mandatory;
    overflow-y: scroll;
    height: 100vh;
    justify-content: center;
    align-items: center;
  }
  .one {
    background-color: rgb(36,164,138);
  }
  .three{
    background-color: rgb(211,79,79);
  }
  .two{
    background-color: rgb(67,97,175);
  }
  .timer-heading {
    font-size: 25px; 
    font-weight: bold;
    /* font-family: 'Roboto'; */
    color: #000000;
    text-align: center;
    justify-content: center;
    align-items: center;
  }

  button {
    margin: 30px;
    font-size: 20px;
    font-weight: bold;
    height: 65px;
    width: 250px;
    border-radius: 10px;
    border: none;
    box-shadow: 1px 1px 0px 2px rgba (0,0,0,0.3);
     cursor: pointer;
   }
   .btn{
     border: 10px;
     border-color: #2196F3;
   }
   .btn:hover{
    background: rgb(67,97,175);
    color: white;
   }

